<template>
    <div>
        <blog-header></blog-header>
        <hr/>
        <div>
            <div>
            <p>用户名：</p>
            <input type="text" v-model="loginInfoVo.username" placeholder="请输入用户名" />
            </div>
        <br/>
            
            <div class="inputbox">
                <p>密码：</p>
                <input type="password" v-model="loginInfoVo.password" placeholder="请输入密码" />
            </div>
        <br/>
            <!-- @click -->
            <button v-on:click="login">登录</button>
        <br/>
            <div>
                <p>登录验证情况</p>
                <textarea cols="30" rows="10" v-model="responseResult" />
            </div>
        </div>
        <hr/>
        <blog-footer></blog-footer>
    </div>    
</template>

<script>
    import blogHeader from '@/components/BlogHeader'
    import blogFooter from '@/components/BlogFooter'


    export default {
        name:'BlogLogin',
        components:{
            blogHeader,
            blogFooter
        },
        data(){
            return {
                loginInfoVo:{
                    username:"",
                    password:""
                },
                responseResult:[]
            }
        },
        methods:{
            login(){
                this.responseResult = "username:" + this.loginInfoVo.username;
                this.responseResult += " || password:" + this.loginInfoVo.password;
                this.$axios.post('/login',{
                    username:this.loginInfoVo.username,
                    password:this.loginInfoVo.password

                })
                .then(successResponse =>{
                    this.responseResult += JSON.stringify(successResponse.data)
                    if(successResponse.data.code === 200){
                        
                        //this.$router.replace({path: '/index'})
                    }
                })
                .catch(failResponse => {})
            }
        }
    }
</script>

<style>
  .inputbox{
      float: inherit;
      text-align: center;
    }
</style>



